#navigationHolder {
  position: absolute;
  height: 100vh;
  width: 100vw;
  background-color: rgba(245, 245, 220, 0.365);
  opacity: 0;
  user-select: none;
  z-index: 2;
  transition: opacity 1s ease-in-out;
}

.button{
  position: absolute;
  height: 35px;
  bottom: 16.2vh;
  transform: rotate(-45deg) scale(100%);
  transform-origin: 0% 0%;
  user-select: none;
  filter: blur(3px);
  transition: all .25s ease-in-out;
}

.button:hover{
  filter: blur(0px);
  transform: rotate(-45deg) scale(105%) translateX(5px);
}

#aboutButton{
  left: 20.9vw;
}

#portfolioButton{
  left: calc(20.9vw + 60px);
}

#servicesButton{
  left: calc(20.9vw + 120px);
}

#contactsButton{
  left: calc(20.9vw + 180px);
}

.button p {
  font-family: 'Cocogoose Regular';
  font-size: 19px;
  margin: 0px;
  color: #d0c6afbb;
}