#loaderHolder{
  position: absolute;
  margin: 0px;
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 1;
  z-index: 1;
  transition: opacity 1s ease-in-out;
}

#loaderHolder p {
  font-family: 'Pixeled';
  font-size: .7em;
  margin: 0px;
  padding: 0px 0px 150px 0px;
}

@keyframes blink{
  0% {
    opacity: 0;
  }
  10% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  70% {
    opacity: 0;
  }
  100% {
    opacity: 0;
  }
}

.loading span {
  animation-name: blink;
  animation-duration: .9s;
  animation-iteration-count: infinite;
}

.loading span:nth-child(2){
  animation-delay: .1s;
}

.loading span:nth-child(3){
  animation-delay: .2s;
}